import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Page1 from '../views/Page1.vue'
import TestPage from '../views/TestPage.vue'
import IDMS_Home from '../views/GPMIDMS/Home.vue'
import GPMOffice from '../views/GPMOffice.vue'


Vue.use(VueRouter)
const routes = [
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/IDMS/Home',
    name: 'IDMS_Home',
    component: IDMS_Home,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/Page1',
    name: 'Page1',
    component: Page1,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/TestPage',
    name: 'TestPage',
    component: TestPage,
    meta: {
      keepAlive: true
    }
  },
  {
    path: '/Office',
    name: 'Office',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: GPMOffice,
    meta: {
      keepAlive: true
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
