<template>
  <div class="KXView">
    <div>
      <p>IP:{{ KxTestSta.IP }}</p>
      <p>連線狀態:{{ KxTestSta.ModuleConnected }}</p>
      <p>資料長度設定:{{ KxTestSta.DataLenSet }}</p>
      <p>量測範圍設定:{{ KxTestSta.MEASRangeSet }}</p>
      <p>GETDATA 請求次數:{{ KxTestSta.SendRequestNumber }}</p>
      <p>模組正常回傳次數:{{ KxTestSta.DeviceReplyOKNumber }}</p>
      <p>Error Code:{{ KxTestSta.ErrorCode }}</p>
      <p>量測時間:{{ KxTestSta.MeasureTime }}</p>
    </div>
    <el-button type="primary" @click="UpdateData">更新</el-button>
  </div>
</template>

<script>
import { apiGetKxTestData } from "../apis/Test.js";
export default {
  data() {
    return {
      KxTestSta: {
        IP: "",
        ModuleConnected: false,
        DataLenSet: 512,
        MEASRangeSet: "8G",
        SendRequestNumber: -1,
        DeviceReplyOKNumber: -1,
        ErrorCode: -1,
        MeasureTime: -1,
      },
    };
  },
  methods: {
    async UpdateData() {
      let serve_reply = await apiGetKxTestData();
      console.log(serve_reply);
      this.KxTestSta = serve_reply.data;
    },
  },
};
</script>

